import React,{ useEffect } from "react";
import { Sidebar, Menu, MenuItem } from 'react-pro-sidebar';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import "./App.css";
import surah  from './surah.json';
 class  Homepage extends React.Component {

   quranImages = () => {
    var imgs =[]
    for(var i=1;i<=624;i++){
      imgs.push(i)
    }
    return imgs 
  }
  goToPage = (event) => {
    if(event.target.value<=604){
      this.setState({
        selected: parseInt(event.target.value)+2,
    })
    }


    }
    goToSura = (event) => {
        this.setState({
          selected: parseInt(event.target.value)+2,
      })
      }
      goToJuz2 = (event) => {
        if(event.target.value==1){
          this.setState({
            selected: 3,
        })    
        }else{
          this.setState({
            selected: parseInt(event.target.value)*20+4-20,
        })
        }

      }
  quranJuz2 = () => {
    var imgs =[]
    for(var i=1;i<=30;i++){
      imgs.push(i)
    }
    return imgs
  }
  quranSurah = () => {
    var suwar=[];
    for(var k in surah) {
      suwar.push(surah[k])      
   }
    return suwar
  }
   
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            menuopen: true,
            selected: 1,
            imgs: this.quranImages(),
            juz2: this.quranJuz2(),
            allsura: this.quranSurah(),
          
          };
          this.quranImages()
          setTimeout(() => {
            this.setState({
                loading: false,
                menuopen: true

            })
          }, 5000);
           }
           toggleIsActive = () => {
            this.setState({menuopen : !this.state.menuopen});
           }
    render() {
        return (
            <div>
            {this.state.loading  ? (
              <div className="app">
              <div className="app2">
              <img className="splash" src="mushaf/splash.jpg" />
              <h1 className="cntr">مصحف المدينة المنورة كاملاً</h1>

                </div>

                </div>
                  ) : (
                    <div className="app">
                    <Sidebar className="sdmn" transitionDuration={2} collapsedWidth="0px" width="50%" backgroundColor="#123259" color="white" collapsed={this.state.menuopen} rtl={true}
                    >
                      <div className="side" >
                    <Menu
                    
                    >
                    <p>
                   Go to Page ذهاب إلي الصفحة
                   </p>
                    <input onChange={this.goToPage} type="number" min={1} max={624} className="inp" />

                    <p>
                   Go to Juz2 ذهاب إلي الجزء
                   </p>
                   <select onChange={this.goToJuz2}>
                   {this.state.juz2.map((data) => (
  <option className="opt" value={data}>{data}</option>
  ))}

</select>                    <p>
                   Go to surat  ذهاب إلي السورة
                   </p>
                   <select onChange={this.goToSura}>
                    
                   {this.state.allsura.map((data)=>(
                  <option  className="opt" value={parseInt(data.pages)}>{data.title+"  "+data.titleAr}</option> 
                  ))}
</select> 
<p className="ftr">تصميم و برمجة المبرمج  محمد عبد المنعم</p>
      <p className="ftr">أسال الله العظيم ان يتقبل مني هذا العمل خالصاُ لوجهه الكريم</p>                
   </Menu>
                  </div>
                </Sidebar> 
                <img onClick={this.toggleIsActive} className="menu" src="./mushaf/logo.png"  />

                <Carousel className="quran"
                swipeable={true}
                showThumbs={false}
                showIndicators={false}
                showStatus={false}
                useKeyboardArrows={true}
                selectedItem={this.state.selected}
>
{this.state.imgs.map((data) => (
        <div>
          <img className="quranimg" key={data} src={`./mushaf/standard2-quran_${data}.png`} />
                  </div>
      
      ))}

</Carousel>
               </div>                
                )               
      }

      </div>
        )
    }
}
    export default Homepage;

      
     